.Logo {
    display: flex;
    text-align: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 24px;
}

.Logo h1 {
    cursor: pointer;
    color: #2196f3;
    text-shadow: 0 0 10px #2196f3;
}

.firstLetter {
    color: #2196f3;
    text-shadow: 0 0 20px #2196f3, 0 0 40px #2196f3;
    animation: blink 0.1s infinite;
}

@keyframes blink {
    from { opacity: 1; /* Непрозрачный текст */ }
    to { opacity: 0; /* Прозрачный текст */ }
   }


.header_block {
    display:flex;
    
}
.header_block ul {
    display:flex;
    text-align: center;
}

.header_block ul li {
    text-decoration: none;
    list-style-type: none;
}

.header_block ul li a{
    position: relative;
    display: inline-block;
    text-transform: uppercase;
    font-size: 24px;
    overflow: hidden;
    letter-spacing: 2px;
    text-decoration: none;
    list-style-type: none;
    margin: 30px;
    padding: 10px;
    border: 1px solid #2196f3;
    border-radius: 5px;
    color: #2196f3;
    transition: 0.2s;
}
.header_block ul li a:hover {
    
    background: #2196f3;
    color: #495468;
    box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
}