.ButtonAdd {
    position: relative;
    display: flex;
    text-align: center;
    justify-content: center;
}

.ButtonAdd a{
    position: relative;
    display: inline-block;
    padding: 10px 30px;
    text-decoration: none;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.4);
    background: #262c37;
    letter-spacing: 2px;
    font-size: 20px;
    transition: 0.5s;
}

.ButtonAdd a:hover{
    color: rgba(255, 255, 255, 1);
    background: #2d3441;
}

.ButtonAdd a span {
    display: block;
    position: absolute;
    background: #2894ff;
}

.ButtonAdd a span:nth-child(1)
{
    left:0;
    bottom: 0;
    width: 1px;
    height: 100%;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 0.5s;
}

.ButtonAdd a:hover span:nth-child(1)
{
    transform: scaleY(1);
    transform-origin: bottom;
    transition: transform 0.5s;
}

.ButtonAdd a span:nth-child(2)
{
    left:0;
    bottom: 0;
    width: 100%;
    height: 1px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s;
}

.ButtonAdd a:hover span:nth-child(2)
{
    transform: scaleY(1);
    transform-origin: left;
    transition: transform 0.5s;
    
}

.ButtonAdd a span:nth-child(3)
{
    right:0;
    bottom: 0;
    width: 1px;
    height: 100%;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 0.5s;
    transition-delay: 0.5s;
    
}

.ButtonAdd a:hover span:nth-child(3)
{
    transform: scaleY(1);
    transform-origin: bottom;
    transition: transform 0.5s;
    transition-delay: 0.5s;
}


.ButtonAdd a span:nth-child(4)
{
    top:0;
    left: 0;
    width: 100%;
    height: 1px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s;
    transition-delay: 0.5s;
}

.ButtonAdd a:hover span:nth-child(4)
{
    transform: scaleY(1);
    transform-origin: left;
    transition: transform 0.5s;
    transition-delay: 0.5s;
}

