.listOfBusines {
    position: relative;
    
    text-align: center;
    justify-content: center;
    color: #ffffff;
    
}
.listOfBusines li {
    text-decoration: none;
    list-style: none;
    font-size: 20px;
    margin: 15px;
    border: 3px solid #262c37;
    padding: 10px;
    transition: 0.5s;
}

.listOfBusines li:hover {
    cursor: pointer;
    background-color: #262c37;
    border: 3px solid #2894ff;
}

.listOfBusines input {
    
}

.red {
    color: tomato;
}
.green {
    color:#D1E231;
    text-decoration: line-through;
}

h3 {
    color: #ffffff;
}
